@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,300;0,600;0,700;0,800;1,300;1,600;1,700;1,800&display=swap);
* {
  font-family: 'Nunito Sans', sans-serif;
}

:root {
  --primary: #6379F4
}


.bg-theme {
    background-color: #6379F4 !important; 
    background-image: url(/static/media/bg-layout-walletchip.b54c0417.png);
    background-size: cover;
    background-position: center;
}

.walletchip-logo {
    height: 40px !important;
    width: auto;
}

.review-app-photo {
    height: 350px;
    width: auto;
    margin-top: 50px;
}

.nav-landing {
  padding-top: 3rem !important;
  padding-bottom: 3rem !important;
  transition: 0.4s;
}

.smaller-nav {
  padding-top: 1.5rem !important;
  padding-bottom: 1.5rem !important;
  background-color: white !important;
}

.smaller-nav .btn:hover {
  color: white !important;
}

.smaller-nav h6 {
  color: black;
  text-decoration: none;
}

.smaller-nav small {
  color: black;
  text-decoration: none;
}

.nav-item > .btn {
  width: 105px;
  border-radius: 10px;
  font-weight: bold;
}

.nav-item > .btn:hover {
  color: var(--primary);
}

.nav-item > .btn-light {
  color: var(--primary);
}

.img-avatar {
  width: 45px;
  height: 45px;
  border-radius: 10px;
}

.personal-info > small {
  font-size: 12px;
}

.personal-info > h6 {
  font-size: 16px;
}
.eye-icon {
    right: 0px !important;
    cursor: pointer;
}

.btn-gray{
    background-color: #DADADA;
}

.outline-none:focus {
    outline: none !important;
    box-shadow: none;
}
.pin-form-costum {
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    width: 53px !important;
    height:  65px !important;
    padding-left: 20px;
    border-radius: 10px;
    border: 1.5px solid #A9A9A9;
}
/* Main navbar Config */
.personal-info {
  color: #3A3D42;
  text-decoration: none;
}

.personal-info:hover {
  color: var(--primary);
}

.nav-main {
  height: 15vh;
  border-bottom-left-radius: 25px;
  border-bottom-right-radius: 25px;
}

/* Main body Config */
.main-body {
  background-color: #FAFCFE !important;
  height: inherit;
}

.left-nav {
  width: 100%;
}

.left-nav > .nav-item.active {
  border-left: solid var(--primary) 4px;
}

.left-nav > .nav-item.active > .nav-link {
  color: var(--primary) !important;
  font-weight: 700;
}

.left-nav > .nav-item {
  border-left: solid transparent 4px;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  height: 35px;
  width: 100% !important;
}

.left-nav > .nav-item:hover > .nav-link {
  color: var(--primary) !important;
}

.left-nav > .nav-item > .nav-link {
  margin-left: 1.5rem;
  color: #3A3D42 !important;
}

/* Bell Dropdown Config */
.dropdown-menu {
  border-radius: 20px;
  width: 350px;
}

.dropdown-menu > small, p {
  color: #7A7886;
}
.footer {
  background-color: var(--primary);
  color: white;
}
body {
  overflow-x: hidden;
}

.card {
  border-radius: 25px;
  border: none;
}

/* Main Hero Config */
.main-hero {
  max-height: 100vh;
  padding-top: 90px;
  background: url(/static/media/hero-landing-wave.aa8585f5.png);
  background-color: var(--primary);
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
}

/* Company Partners Config */
.company-partners {
  background-color: #F4F3FD;
  height: 140px;
  position: relative;
}

/* About App Config */
.about-app {
  background-color: #FAFCFE;
}

.features-card > .card {
  box-shadow: 10px 10px 100px -74px rgba(0,0,0,0.75);
}

/* Great Features Config */
.great-features {
  background: rgba(71, 58, 209, 0.06);
}

/* Testimoni Config */
.testimoni {
  background-color: #FAFCFE;
}

.users-experience {
  box-shadow: 1px 10px 64px -43px rgba(0,0,0,0.75);
}

/* Footer Config */
.footer-landing {
  background-color: var(--primary);
}

.footer-landing hr {
  border: solid white 1px;
}
.card-balance {
    background-color: #6379F4 !important;
}

.btn-balance-control {
    background: rgba(255, 255, 255, 0.2);
}

.img-profile-avatar {
    width: 70px !important;
    height: auto;
    /* height: 60px !important; */
}

.transfer-color {
    color: #1EC15F;
}

.subsription-color {
    color: #FF5B37;
}

.card-transaction-history {
    height: 235px;

}

.width-100 {
    height: 100%;
}
.card-history {
  max-height: 65vh;
}
.border-rad {
    border-radius: 20px;
}

.border-rad_input {
    border-radius: 12px;
}

.img-profile {
    width: 50px !important;
    height: 50px !important;
}


.card-list-user {
    height: 318px;
}

.pagination-bar {
    width: 150px;
    height: 40px;
}
.card-topup {
    height: 442px;
}
/* profile image config */
#picture {
  opacity: 0;
  position: absolute;
  z-index: -1;
}

.profile-img {
  
}

.profile-img > img {
  width: 100px;
  height: 100px;
  border-radius: 15px;
}

.btn-profile-navigation {
  border-radius: 10px;
  color: #4D4B57;
  background-color: #E5E8ED;
}

.btn-profile-navigation:hover {
  background-color: #d2d5dd;
}
.form-change-password input {
  border: 0;
  padding-left: 2rem;
  border-bottom: 2px solid rgba(169, 169, 169, 0.6);
}

.form-change-password input::-webkit-input-placeholder {
  color: #A9A9A9;
}

.form-change-password input:-ms-input-placeholder {
  color: #A9A9A9;
}

.form-change-password input::placeholder {
  color: #A9A9A9;
}

.form-change-password input:focus, button:focus {
  outline: none;
}

.focused-input {
  border-bottom: solid var(--primary) 2px !important;
}

.card-confirmation {
    height: 305px;
}
.border-rad {
    border-radius: 20px;
}

.btn-blue {
    background-color: #6379F4;
    border-radius: 12px !important;
}
.success-icon {
    height: 70px;
    width: auto;
}

.btn-gray {
    background: rgba(99, 121, 244, 0.15);
    border-radius: 12px;
}

.text-blue {
    color: #6379F4;
}

.card-transfer-success {
    height: 356px;
}

.card-transfer-failed {
    height: 301px;
}
